
body{
    background-color: #3C3F64;
}

.Hangman{
    width: 70%;
    height: 500px;
    background-color: white;
    position: relative;
    border-radius: 1%;
    overflow: hidden;
}
.title{
font-size: 38px;
font-weight: 900;
color: rgb(67, 65, 78)
}
.Gallow{
    position: relative;
  width: 180px;
  height: 15px;
  background-color: rgb(59, 58, 58);
  top: 10px;
  border-radius: 0 5px 5px 0;
}
.DownPipe{
    position: absolute;
    left: 140px;
    width: 15px;
    height: 40px;
    background-color: rgb(59, 58, 58);
    border-radius: 5px;
}
.text-error{
    position: absolute;
    right: 50px;
    width: 40%;
}
.you_missed_text{
    font-size:12px;
    font-weight: 900;
    color: #53555D;
}
.error_letter{
    font-size: 35px;
    letter-spacing: 7px;
    font-weight: 900;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #585DFE;
}
.text-letter{
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
   width: 100%;
}
.RightBlueTriangle{
  position: absolute;
  width: 0;
  height: 0;
  right: 0;
  bottom: 0;
  border-style: solid;
  border-width: 0 0 250px 250px;
  border-color: transparent transparent #585DFE transparent;
  z-index: 10;
}
.btn-lg{
    z-index: 10 !important;
}
.correctWord{
    display: flex;
    flex-direction: column;
    border: solid 1px white;
    height: 5px;
    opacity: 0;
}
.block{
    background: rgba(15, 15, 15, 0.582);
    height: 100% ;
    width: 100% ;
    position: fixed;
    top: 0;
    z-index: 90;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.none{
    display: none;
}
.boxanswer{
    position: absolute;
    bottom: 20px;
    width: 100%;
    margin:0 auto;
    background-color: chartreuse;
    z-index: 40;
    text-align: center;
}
.btn-new-game{
    text-transform: uppercase;
  border: dashed 2px #FFB800;
  border-radius: 25px;
  background-color: transparent;
  color: #FFB800;
  padding: 15px 40px;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
}
.btn-new-game:hover{
    opacity: 0.6;
    color: #FFB800;
    border: solid 2px #FFB800;
    background-color: white;
}
.game-status{
    text-transform: uppercase;
  color: white;
  font-size: 35px;
}
/* Medium devices (LargeScreen, 992px and up) */
@media (min-width: 993px) {
    .Hangman{
        position: absolute; 
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 90%;
    }
}

/* Medium devices (tablets, 768px and up) */
@media (max-width: 768px) {
    .Hangman{
        width: 85%;
        background-color: white;
        position: relative;
        border-radius: 1%;
        overflow: hidden;
        position: absolute; 
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 70%;
       
    } 
    
}

/* Medium devices (SmartPhone, 576px and up) */
@media (max-width: 576px){
    .Hangman{
        width: 90%;
    }
   .title{
       font-size: 20px;
   }
   .Gallow{
