    border-radius: 0 5px 5px 0; 
   }
   .DownPipe{
    position: absolute;
    left: 90px;
    width: 10px;
    height: 30px;
    background-color: rgb(59, 58, 58);
    border-radius: 5px;
   }
   .text-error{
    position: absolute;
    right: 20px;
