   }
   .error_letter{
    font-size: 15px;
    letter-spacing: 7px;
    font-weight: 900;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #585DFE;
    }
    .you_missed_text{
